define('org/forgerock/openam/ui/common/util/ExternalLinks',["lodash"], function (_) {
    var backstageDocsUrl = "https://backstage.forgerock.com/#!/docs/openam/13.5/";

    return {
        backstage: {
            authz: _.mapValues({
                policySets: "#configure-apps-with-console",
                policies: "#configure-policies-with-console",
                resourceTypes: "#configure-resource-types-with-console"
            }, function (hash) {
                return backstageDocsUrl + "admin-guide" + hash;
            }),
            config: {
                services: backstageDocsUrl + "reference#chap-config-ref"
            }
        }
    };
});

